import React, { useState, useReducer } from 'react'
import backgroundImg from './img/bg-desktop-light.jpg'
import './App.css';
import Header from './components/Header';
import TodoList from './components/TodoList';

export const todolistContext = React.createContext()
function App() {
  let initialState;
  if (localStorage.getItem('todos')) {
    initialState = JSON.parse(localStorage.getItem('todos'))
  } else {
    initialState = [
      {
        "name": "Complete online js course",
        "completed": true,
        "active": true,
        "key": 1

      },
      {
        "name": "making todo",
        "completed": true,
        "active": false,
        "key": 2

      },
      {
        "name": "Another React app",
        "completed": false,
        "active": true,
        "key": 3

      }
    ];
  }
  const reducer = (currentState, action) => {
    switch (action.type) {
      case 'remove':
        return action.newarray;
      case 'add':
        return action.newarray;
      case 'changename':
        return action.newarray;
      case 'completed':
        return action.newarray;
      case 'active':
        return action.newarray;
      default:
        return currentState;

    }
  }
  const [todo, dispatch] = useReducer(reducer, initialState);
  const [day, night] = useState(1);///for theme
  const [filter, setFilter] = useState(0);/// used to filter is clicked or not
  const [filtertodo, setfiltertodo] = useState([]);///filter todolist
  const [todoname, setTodoName] = useState(""); /// todo name


  /// (filtertodolist) function takes argument as filteroption and filter todo array based on that
  let filtertodolist = (filterOption) => {
    return todo.filter(function (item) {
      if (filterOption === "active" && item.active) {
        return item;
      }
      if (filterOption === "completed" && item.completed) {
        return item;
      }
      if (filterOption === "clear" && !item.completed) {
        return item;
      }
    })
  }

  /// submit the form to add new aaray in list
  const submithandel = (e) => {
    let key = `${Math.floor(Math.random() * 1000 + todo.length + 1)}${todoname}`;
    e.preventDefault();
    dispatch({
      type: "add", newarray: [...todo, {
        "name": todoname,
        "completed": false,
        "active": true,
        "key": key
      }]
    })
    localStorage.setItem('todos', JSON.stringify([...todo, {
      "name": todoname,
      "completed": false,
      "active": true,
      "key": key
    }]));
    setTodoName("");
  }
  return (
    <div className="App" >
      <img src={backgroundImg} alt="" width="100%" />
      <div className="App-container">
        <Header title="TODO" />
        <form className="Form" onSubmit={submithandel}>
          <button aria-label="Add new item" type="submit" className="input-check"
            onSubmit={submithandel} ></button>
          <input
            type="text"
            required
            placeholder="create a new todo"
            className="Form-input"
            autoFocus
            value={todoname}
            onChange={function (e) { { setTodoName(e.target.value) } }}
          />
        </form>
        <div className="Todolist-wrapper">
          <todolistContext.Provider value={{ todoa: filter ? filtertodo : todo, tdispatch: dispatch }}>
            <TodoList />
          </todolistContext.Provider>
          <div className="TodoFooter">
            <p>{filter ? filtertodo.length : todo.length} items</p>
            <div className="flex">
              <span onClick={() => { setFilter(0) }}>All</span>
              <span onClick={() => { setFilter(1); setfiltertodo(filtertodolist("active")) }}>Active</span>
              <span onClick={() => { setFilter(1); setfiltertodo(filtertodolist("completed")) }}>Completed</span>
            </div>
            <span onClick={() => {
              setFilter(0);
              dispatch({ type: "completed", newarray: filtertodolist("clear") })
              localStorage.setItem('todos', JSON.stringify([...filtertodolist("clear")
              ]));
            }}>clear completed </span>
          </div>
          <p className="dragdropmsg">Drag and drop to reorder the list</p>
        </div >
      </div>
    </div>
  );
}

export default App;
