import React, { useState, useContext } from 'react';
import { todolistContext } from "../App";
import cross from '../img/icon-cross.svg'
function TodoList() {
    const todolist = useContext(todolistContext);

    const deletetodo = (e) => {
        let newtodolist = todolist.todoa.filter(todo => {
            if (todo.key !== e) {
                return todo;
            }
        })
        todolist.tdispatch({ type: "remove", newarray: newtodolist })
        localStorage.setItem('todos', JSON.stringify([...newtodolist]));
    }

    const changeCompleteStatus = (e) => {
        let newtodolist = todolist.todoa.map(todo => {
            if (todo.key === e) {
                todo.completed = !todo.completed;
            }
            return todo;
        });
        todolist.tdispatch({ type: "completed", newarray: newtodolist })
        localStorage.setItem('todos', JSON.stringify([...newtodolist]));
    }
    const changeActiveStatus = (e) => {
        let newtodolist = todolist.todoa.map(todo => {
            if (todo.key === e) {
                todo.active = !todo.active;
            }
            return todo;
        });
        todolist.tdispatch({ type: "active", newarray: newtodolist })
        localStorage.setItem('todos', JSON.stringify([...newtodolist]));
    }

    return (
        <>
            {todolist.todoa.map((todo) => (
                <div className="TodoList" key={todo.key}>
                    <button aria-label="Add new item" className={`input-check ${todo.completed ? 'checked' : ''}`} onClick={() => changeCompleteStatus(todo.key)}>
                    </button>
                    <p onClick={() => changeActiveStatus(todo.key)} className={`Form-input ${!todo.active ? "strikethrouw" : ''}`}>{todo.name}</p>
                    <img src={cross} alt="" className="delete" onClick={() => deletetodo(todo.key)} />
                </div>
            ))}
        </>

    )
}

export default TodoList
